import {Segment} from 'base-segment';
// #------------------------------------------# //
// #---- Segment (/index/segment-sidebar) ----# //

class Sidebar extends Segment {
	/*@ngInject*/

	controller($injector, $scope, ssPermissionsSvc, ssFlagCCSvc) {
		// view model that is shared by all segments on this route,
		// used for segment coordination

		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentIndexSidebarLogic');// jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
			canViewOthers: ssPermissionsSvc.isPermittedAction('bundle', 'viewothers'),
			// Release notes are in the dashboard for now because, well, angularJS isn't controlled by us and this repo is controlled by FC,
			//	so we can update notes and release without really any delays. Dashboards isn't really changed much anymore...
			releaseNotes: [{
					note: 'One of your most requested enhancements has been for improved functionality around Form Denial workflow, so we are thrilled to announce our new and improved Form Denial capabilities! You can now select whether you want to Deny & Complete the form, or Deny & Send Back to any previous step; include prominent notes as to why the form was denied; and easily track Denied forms in the In Progress and Completed tabs. Email notifications have been enhanced to include denial details for your users. For any forms denied prior to these enhancements, you can even go back into those forms and select those new actions. See more details in the Learning Center.',
					isAdmin: true,
					releaseDate: '2020-07-30',
					learningCenterId: '360050369054',
					featureFlag: {
						name: 'fc_enhanced_denied_form_workflow',
						showIfActive: true
					}
				}, {
					note: '<p>We are pleased to announce additional COVID-19 forms in our shared forms library. Forms include employee health surveys, notices, emergency leave requests, and several others! If you would like these forms copied to your Frontline Central system, please contact the support team. To see all of the forms we currently have available in our library checkout <a target="_blank" href="https://www.frontlineeducation.com/central-forms-library">frontlineeducation.com/central-forms-library</a>.</p>',
					isAdmin: true,
					releaseDate: '2020-06-24',
					renderNoteAsHtml: true
				},{
					note: '<div><h5>New Pay Schedule and Enhanced Assignment Import Types</h5><p>We are excited to announce a new pay schedule import template. You can now import pay schedules and associate them with the appropriate job type in the system. In addition, we’ve updated the assignment import to support assignment external IDs to uniquely identify an employee’s assignment and keep updates from third-party systems synchronized with Frontline Central. If you don’t have the need to include a unique assignment external ID, no problem! The importer will only make updates to existing assignments if we see something has changed, like a location, or department, or position name. Learning center materials will be coming soon. If you have questions, please reach out to our support team.</p></div>',
					isAdmin: true,
					releaseDate: '2020-06-18',
					renderNoteAsHtml: true
				},{
					note: 'Administrative users now have the ability to import and update data stored in Frontline Central by using the Data Importer tool. To learn more about the Data Importer, please see the following article in the Learning Center.',
					releaseDate: '2020-05-11',
					isAdmin: true,
					learningCenterId: '360033315734-Data-Import-Overview?flash_digest=7511ccc9c5e5df2147275dcb593e5180b4e4656c',
					featureFlag: {
						name: 'global_hrms',
						showIfActive: false
					}
				},{
					note: `<div><h5>Automated Form Workflow Reminders:</h5><p>Frontline Central will automatically send a reminder three days before a form's due date. The reminder will be sent automatically to the step participant who needs to take immediate action to move the form to the next step. Automated form reminders will be available for all forms sent after April 30th, 2020.</p></div>`,
					releaseDate: '2020-04-29',
					isAdmin: true,
					renderNoteAsHtml: true,
					learningCenterId: '115003574448-Tracking-Forms?flash_digest=fd8f12383019e4282ccca8e32c33ff796aaebee6'
				}, {
					note: '<div><h5>Now Available:</h5><ul><li>&bull; The ability to display two or more secondary assignments on a form.</li><li>&bull; The ability to summarize salary totals for primary assignments, secondary assignments, or both on a form</li><li>&bull; The ability to display an employee\'s credentials (certifications, licenses, clearances, etc.) on a form.</li><li>&bull; Enhancements to the "text" fields in forms; including the ability to adjust the font size, bold/italicize/underline, embed tables, and pull different field types into a line of text.</li><li>&bull; The ability to pull an administrative signature onto a form template, so that you can display their signature on each instance of the form without needing them to sign each one individually.</li></ul></div>',
					releaseDate: '2020-03-31',
					isAdmin: true,
					renderNoteAsHtml: true
				}, {
					note: 'In response to the concern and uncertainty the world continues to face, the Families First Coronavirus Response Act (FFCRA) was signed into federal law on March 13th, 2020. The bill, which will take effect on April 1st, introduces new requirements for certain employers to provide their employees with paid sick leave and expanded family and medical leave for issues related to COVID-19. For this reason, you can now submit a request to our Support team to have the FFCRA Acknowledgement added to your Form Template Library. This new form will allow you to digitally send, track, and store, each employee’s acknowledgement of these new guidelines.',
					releaseDate: '2020-03-31',
					isAdmin: true,
				}, {
					note: 'When a new employee is created in Frontline Central, they will now automatically be granted access to the product within the Manage User Access Page. However, you will still have to send an invitation to the new user. For additional details, please see the following learning center article.',
					releaseDate: '2020-01-29',
					isAdmin: true,
					learningCenterId: '115015989948-Manage-User-Access-?flash_digest=8ae8305a3c5478093ce4a4f99ff10525db74963c'
				}, {
					note: 'Frontline Central now supports customizable form field labels in the form builder. This feature will provide added flexibility to make the verbiage that appears on the form match the terms you use in your district. Please see the following learning center article for more information.',
					releaseDate: '2020-01-14',
					isAdmin: true,
					learningCenterId: '115003457967?flash_digest=6f3f219c6dfba263dee11ec5d45abd96b3e21804'
				}, {
					note: 'The employee profile now supports sectional edits. This slight change will better support updates to specific parts of related information on the page. When you click the edit button on a section of the page, that section will open for edit, and a save button will appear. Each section can be edited and saved individually.',
					releaseDate: '2019-11-06',
					isAdmin: true,
				}, {
					note: 'Export templates are now available for Employee Data and Employee Assignment Reports. Export templates give administrative users the ability to configure how you want the data coming out of Frontline Central to look. You can select the desired delimiter from several predefined options, indicate if you want the export to include all current records, or just newly added or edited records. Finally, you can rename, reorder, or hide columns and column headers. If you are interested in setting up automated nightly exports, please contact our support team.',
					releaseDate: '2019-10-17',
					isAdmin: true,
					learningCenterId: '360036855894?flash_digest=1e518b1a41c55f90a39a8fc1b56fe0520d29736f'
				}, {
					note: 'We\'re excited to announce that we can now provide Live Chat support for Super Admins. If you are a Super Admin and would like support for Frontline Central, please click the question mark icon in the upper right, then "Frontline Support", and then "Start a Chat". Our support hours are from 8:00am to 5:00pm EST.',
					releaseDate: '2019-10-10',
					isAdmin: false
			}]
		};

		vm.releaseNotes = vm.releaseNotes.filter(checkFeatureFlag)
		
		function checkFeatureFlag(field){
			if (field.featureFlag) {
				return routeVm.featureFlags[field.featureFlag.name] === field.featureFlag.showIfActive;
			}
			return true;
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line

		};
	}
}

// #-- END Segment (/index/segment-sidebar) --# //
// #------------------------------------------# //

angular.module('ec-dashboard-segments').directive('segmentIndexSidebar', () => new Sidebar('sidebar') );
